function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        speed: 1200,
        effect: 'fade',
        autoplay:{
          delay: 5000
        },
        allowTouchMove: false,
        loop: true,
        // fadeEffect: {
        //     crossFade: true
        // },
        pagination: {
            el: '.main .swiper-pagination',
            clickable: true,
            renderBullet: function (index, className) {
                let num = getPaginationNum(index+1)
                return `<span class="swiper-pagination-bullet">${num}</span>`;
            },
        }
    })
    function getPaginationNum(num){
        if(num<10){
            return `0${num}`
        }
        return num
    }
}

function newsSlider() {
    var swiper = new Swiper('.news .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 40,
        navigation: {
            nextEl: '.news .swiper-button-next',
            prevEl: '.news .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: "auto",
                spaceBetween: 15
            },
            460: {
                slidesPerView: "auto",
                spaceBetween: 32
            },
            576: {
                slidesPerView: 2,
                spaceBetween: 16
            },
            768: {
                slidesPerView: 3,
                spaceBetween: 16
            },
            1450: {
                slidesPerView: 3,
                spaceBetween: 32
            },
            1600: {
                slidesPerView: 3,
                spaceBetween: 40
            },
        }
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 16,
        slidesPerView: 4,
        freeMode: true,
        watchSlidesProgress: true,
        direction: 'vertical',
        breakpoints: {
            320: {
                slidesPerView: 3,
                spaceBetween: 16
            },
            576: {
                slidesPerView: 4,
                spaceBetween: 16
            },
        }
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        direction: 'vertical',
        thumbs: {
            swiper: swiper,
        },
    });
}

function collectionSlider() {
    var swiper = new Swiper('.collection .swiper-container', {
        slidesPerView: 4,
        spaceBetween: 40,
        navigation: {
            nextEl: '.collection .swiper-button-next',
            prevEl: '.collection .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: "auto",
                spaceBetween: 15
            },
            460: {
                slidesPerView: "auto",
                spaceBetween: 32
            },
            576: {
                slidesPerView: 2,
                spaceBetween: 16
            },
            768: {
                slidesPerView: 3,
                spaceBetween: 16
            },
            1100: {
                slidesPerView: 4,
                spaceBetween: 16
            },
            1450: {
                slidesPerView: 4,
                spaceBetween: 32
            },
            1600: {
                slidesPerView: 4,
                spaceBetween: 40
            },
        }
    })
}

function mobSliders() {
    $(".mob-slider").each(function (index, el) {
        $(el).attr("data-slider-id", `${index}`)
        var swiper = new Swiper(`.mob-slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            breakpoints: {
                320: {
                    spaceBetween: 15
                },
                460: {
                    spaceBetween: 32
                },
                576: {
                    spaceBetween: 0
                }
            }
        })
    });
}



mainSlider()
newsSlider()
gallery()
collectionSlider()
mobSliders()
